* {
    -webkit-font-smoothing: antialiased;
    scroll-behavior: smooth;
}

body {
    background-color: #F9FBFF;
    width: 100vw;
    display: flex;
    margin-right: auto;
    margin-left: auto;
    min-width: 300px;
}

*, *:before, *::after {
    padding: 0;
    margin: 0;
    border: none;
    box-sizing: border-box;
}

a, a:visited, a:hover {
    color: inherit;
    text-decoration: none;
}

a:focus, a:active {
    outline: none
}

h1, h2, h3, h4, h5, h6, p, div {
    background-color: rgba(255, 255, 255, 0);
    font-size: inherit;
    font-weight: inherit;
    line-height: inherit;
    font-family: 'Montserrat';
}

img, svg {
    background-color: rgba(255, 255, 255, 0);
    vertical-align: top;
    height: auto;
}

input, button {
    background-color: rgba(255, 255, 255, 0);
    font-family: inherit;
    font-size: inherit;
    display: inline-block;
    box-shadow: none;
}

button {
    cursor: pointer;
    transition: all .15s;
}

input:focus, input:active, button:focus, button:active {
    outline: none
}

input::placeholder {
    color: rgba(255, 255, 255, 0.38);
}

ul {
    list-style: none;
}

.container {
    width: 100vw;
    margin-left: auto;
    margin-right: auto;
}

:root {
    --color-primary: #415167;
    --color-accent: #C7A17A;
    --color-text: #151d28;
    --color-contrast: #EDF0F5;
    --color-text-contrast: #F9FBFF;
    --color-text-contrast-hover: #ffffff;
    --color-accent-hover: #e0b182;
    --color-modal: #F8F8F8FF;
    --color-modal-background: rgba(237, 240, 245, 0.38);
    --color-second-hover: #F9F5E8;
}