button {
    font-family: 'Nunito';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
}

.buy {
    background: var(--color-accent);
    color: var(--color-text-contrast);
    border-radius: 19px;
    padding: 7px 15px 7px 17px;
    margin-right: 1.2857vw;
    height: 32px;
}

.buy:hover {
    background-color: var(--color-accent-hover);
    color: var(--color-text-contrast-hover);
}

.info {
    color: var(--color-primary);
    border-radius: 19px;
    padding: 7px 11px 7px 11px;
    height: 32px;
}

.info:hover {
    background-color: var(--color-contrast);
    color: var(--color-text);
}

.arrow {
    background-color: var(--color-accent);
    border-radius: 100%;
    width: 45px;
    height: 45px;
    padding: 15px 12px 16px 12px;
    display: flex;
}

.arrow.left {
    margin-left: 5.29vw;
}

.arrow.right {
    margin-left: -1.143vw;
}

@media (max-width: 780px) {
    .arrow.right {
        margin-left: -3.143vw;
        margin-right: 2vw;
    }
}

.arrow.left img {
    transform: rotate(180deg);
}

.arrow:hover {
    background-color: var(--color-accent-hover);
}