.giftsetWrapper {
    display: flex;
    flex-direction: column;
    max-width: 1400px;
}

.giftsetBlock {
    background: var(--color-contrast);
    width: 77vw;
    left: -2px;
    margin-top: -214px;
}

.desctiption {
    position: relative;
    display: flex;
    flex-direction: column;
    right: auto;
    margin: 86px auto 66px;
    text-align: center;
    height: 62px;
}

.desctiption h2 {
    color: var(--color-primary);
    font-family: 'Nunito';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
}

.desctiption h1 {
    color: var(--color-primary);
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 900;
    font-size: 30px;
    line-height: 34px;
}

.slider {
    margin-top: 154px;
    display: flex;
    margin-bottom: 100px;
}

.giftCard {
    background-color: var(--color-text-contrast-hover);
    box-shadow: 1px 0px 10px rgba(205, 205, 205, 0.5);
    width: 57vw;
    left: 28vw;
    top: 216px;
    display: flex;
    margin-top: 68px;
    margin-left: 30%;
}

.giftImage {
    margin-left: -150px;
    width: 35vw;
    max-width: 428px;
}

.cardContent {
    margin: 5.6vw 2vw 2vw 0;
    width: 35vw;
    max-width: 800px;
    font-family: 'Nunito';
    font-style: normal;
}

.cardContent h2 {
    color: var(--color-accent);
    font-weight: 600;
    font-size: calc(19px + 1 * (100vw / 1400));
    line-height: 24px;
}

.cardContent h1 {
    color: var(--color-primary);
    margin-top: 1.1vw;
    font-weight: 700;
    font-size: calc(22px + 1 * (100vw / 1400));
    line-height: 28px;
}

.cardContent p {
    color: var(--color-text);
    margin-top: 1vw;
    font-weight: 300;
    font-size: calc(15px + 1 * (100vw / 1400));
    line-height: 22px;
}

.giftProperties {
    margin-top: 5%;
    margin-bottom: 10%;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.giftProperties img {
    width: 40px;
    height: 40px;
}

.giftProperties .property {
    display: flex;
}

.giftProperties h4 {
    color: var(--color-primary);
    font-family: 'Nunito';
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 22px;
}

.giftProperties h3 {
    color: var(--color-primary);
    font-family: 'Nunito';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
}

.buttons {
    display: grid;
    margin-right: 2vw;
    grid-template-columns: 166px 166px;
    grid-column-gap: 3vw;
}

.pages {
    display: grid;
    grid-template-rows: 29% 28% 29%;
    position: relative;
    top: 68px;
}

.pages button {
    background-color: rgba(237, 240, 245, 0.3);
    color: rgba(65, 81, 103, 0.3);
    margin-left: 2px;
    cursor: pointer;
    padding: 62px 33px 62px 36px;
    width: 88px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 900;
    font-size: 30px;
    line-height: 34px;
}

.pages button:hover {
    background-color: rgba(237, 240, 245, 0.15);
    color: rgba(65, 81, 103, 0.4);
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 900;
    font-size: 30px;
    line-height: 34px;
}

.pages button.active {
    background-color: var(--color-text-contrast-hover);;
    color: var(--color-primary);
}

@media (max-width: 1203px) {
    .cardContent {
        margin: 1.6vw 2vw 2vw -3vw;
        width: 35vw;
    }

    .giftProperties {
        margin-top: 1vw;
        margin-bottom: 2vw;
        display: grid;
        grid-template-columns: 6fr 4fr;
    }

    .buttons {
        display: grid;
        margin-right: 2vw;
        grid-template-columns: 166px 166px;
        grid-column-gap: 0;
    }

    .giftCard {
        height: auto;
    }
}

@media (max-width: 981px) {
    .giftCard {
        height: auto;
    }

    .giftProperties {
        display: grid;
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 1fr;
        grid-row-gap: 0.5vw;
    }

    .buttons {
        display: grid;
        margin-right: 2%;
        grid-template-columns: 133px 133px;
        grid-column-gap: 0;
    }
}

@media (max-width: 750px) {
    .slider {
        margin-top: 200px;
        margin-bottom: 60px;
    }

    .giftsetBlock {
        width: 80vw;
        height: 100%;
        left: -2px;
        margin-top: -214px;
    }

    .desctiption {
        position: relative;
        display: flex;
        flex-direction: column;
        right: auto;
        margin: 86px auto 66px;
        text-align: center;
        height: 62px;
    }

    .giftCard {
        width: 80vw;
        margin-left: 7vw;
        margin-top: 0;
        display: flex;
        flex-direction: column;
    }

    .giftImage {
        width: 73vw;
        display: flex;
        margin-left: auto;
        margin-right: auto;
    }

    .cardContent {
        margin: 5.6vw 2vw 2vw 5.6vw;
        width: 60vw;
    }

    .pages {
        display: flex;
        flex-direction: column;
        margin-top: -68px;
    }

    .pages button {
        height: calc(33.33% - 22.5px);
    }
}

@media (max-width: 373px) {
    .buttons {
        display: flex;
        flex-direction: column;
    }
}